"use client";

import { useEffect } from "react";
import Navbar from "@/components/Navbar";
import { useRouter } from "next/navigation";
import { useAuthStore } from "@/store/authStore";

// UI Components (shadcn)
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Separator } from "@/components/ui/separator";

// Icons
import { Users, CheckCircle2, Clock, BadgeAlert } from "lucide-react";

// Toast
import toast from "react-hot-toast";

export default function DashboardPage() {
  const isLoggedIn = useAuthStore((s) => s.isLoggedIn);
  const hydrated = useAuthStore((s) => s.hydrated);
  const router = useRouter();

  // Redirect after hydration
  useEffect(() => {
    if (hydrated && !isLoggedIn) {
      router.push("/login");
    }
  }, [hydrated, isLoggedIn, router]);

  // Show loader until hydration done
  if (!hydrated) {
    return (
      <div className="p-6 font-medium text-slate-600">
        Loading dashboard...
      </div>
    );
  }

  if (!isLoggedIn) return null;

  // Toast ONLY after user is verified and hydrated
  useEffect(() => {
    if (hydrated && isLoggedIn) {
      toast.success("Welcome to the Dashboard!", {
        id: "dashboard-welcome",
      });
    }
  }, [hydrated, isLoggedIn]);

  return (
    <div className="min-h-screen bg-slate-100">
      <Navbar />

      <main className="p-6">
        <h1 className="text-3xl font-bold mb-2">Dashboard Overview</h1>
        <p className="text-slate-600 mb-6">Quick summary of today’s activity</p>

        {/* Stat Cards */}
        <div className="grid gap-6 grid-cols-1 md:grid-cols-2 lg:grid-cols-4">

          {/* Total Employees */}
          <Card className="shadow-sm">
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium text-slate-500">
                Total Employees
              </CardTitle>
              <Users className="h-5 w-5 text-blue-600" />
            </CardHeader>
            <CardContent>
              <div className="text-3xl font-bold">120</div>
            </CardContent>
          </Card>

          {/* Present Today */}
          <Card className="shadow-sm">
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium text-slate-500">
                Present Today
              </CardTitle>
              <CheckCircle2 className="h-5 w-5 text-green-600" />
            </CardHeader>
            <CardContent>
              <div className="text-3xl font-bold">87</div>
            </CardContent>
          </Card>

          {/* On Break */}
          <Card className="shadow-sm">
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium text-slate-500">
                On Break
              </CardTitle>
              <Clock className="h-5 w-5 text-orange-500" />
            </CardHeader>
            <CardContent>
              <div className="text-3xl font-bold">3</div>
            </CardContent>
          </Card>

          {/* Pending Approvals */}
          <Card className="shadow-sm">
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium text-slate-500">
                Pending Approvals
              </CardTitle>
              <BadgeAlert className="h-5 w-5 text-red-600" />
            </CardHeader>
            <CardContent>
              <div className="text-3xl font-bold">3</div>
            </CardContent>
          </Card>

        </div>

        <Separator className="my-10" />

        <h2 className="text-xl font-semibold mb-4">Attendance Summary</h2>
        <p className="text-slate-600 mb-6">
          More sections can go here… (employees table, shifts, logs, etc.)
        </p>

      </main>
    </div>
  );
}
